details {
    margin-top: 10px;
}

details summary {
    font-weight: bold;
}

details div span {
    cursor: pointer;
}

details p,
details div {
    margin: 15px;
    margin-right: unset;

}

details.xyz-create summary span.create-web-app,
span.create-web-app {
    color: #00a600;
}

details.xyz-update summary span.update-web-app,
span.update-web-app {
    color: #ff8400;
}

details.xyz-upgrade summary span.upgrade-web-app,
span.upgrade-web-app {
    color: #f00;
}

.service-details {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
}

.service-details.flex {
    flex-direction: row;
}

.service-details.flex-reverse {
    flex-direction: row-reverse;
}

.service-details .service-picture,
.service-details .service-description {
    width: calc(50% - 12px);
    padding: 5px;
}

.service-img-list {
    width: 100%;
}

.service-picture img,
.service-img-list img {
    width: calc(100% - 8px);
    height: auto;
    margin: 3px;
    outline: 1px solid var(--bc);
}

.service-img-ctrl {
    position: relative;
    top: -50%;
    display: flex;
    justify-content: space-between;
    padding-left: 5px;
    padding-right: 5px;
    align-items: center;
}

.service-img-ctrl-b,
.service-img-ctrl-f {
    background: #80808055;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    cursor: pointer;

}

.service-img-ctrl-b div,
.service-img-ctrl-f div {
    font-size: 30px;
    padding: 0;
    margin: 0;
    margin-top: -5px;
    margin-left: 2px;
}

.service-img-ctrl-b div::before {
    content: "<";
}

.service-img-ctrl-f div::before {
    content: ">";
}

.service-img-counter {
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: relative;
    top: -30px;
}

.service-img-counter div {
    width: 10px;
    height: 10px;
    border: 1px solid gray;
    border-radius: 50%;
    margin: 0 2px;
}

.service-img-counter div.active {
    background: gray;
}

.service-price {
    margin-top: 20px;
}

.service-price .old-price {
    text-decoration: line-through;
    color: #5f5c5c;
    margin-right: 10px;
}

.service-price .new-price {
    font-weight: bold;
    color: #00a600;
    font-size: 20px;
}

.service-price .discount {
    font-weight: bold;
    color: #f00;
    font-style: italic;
    position: relative;
    top: -10px;
}

.service-price .currency {
    font-size: 25px;
}

@media only screen and (max-width: 770px) {
    .service-details.flex-reverse {
        flex-direction: row;
    }

    .service-details .service-picture,
    .service-details .service-description {
        width: 100%;
    }
}