.login-admin {
    padding: 10px;
    width: calc(100% - 20px);
}

.login-admin .sfwe75s4dffd {
    /* animation: name duration timing-function delay iteration-count direction fill-mode; */
    animation: errorBox 3s linear infinite;
    font-weight: bold;
}

.login-admin .content {
    width: 100%;
}

.login-admin input {
    width: 300px;
    margin: 5px 0;
    padding: 3px 5px;
}

.login-admin .btn-holder {
    padding: 15px;
}

.login-admin .btn-holder * {
    padding: 5px;
    margin: 5px;
}

.login-admin .table-holder {
    width: 100%;
    overflow-x: scroll;
}

.login-admin table,
.login-admin th,
.login-admin td {
    border: 1px solid black;
    border-collapse: collapse;
}

.login-admin table {
    width: 100%;
    min-width: 1000px;
}

.login-admin th.email {
    width: 100px !important;
    max-width: 100px !important;
    white-space: wrap;
    word-break: break-word;
    overflow: visible;
}

.login-admin td {
    padding: 4px;
    text-align: center;
}

.login-admin td.msg {
    text-align: left;
    width: 60%;
}

.login-admin td.email {
    width: 50px;
    white-space: wrap;
    word-wrap: break-word;
    overflow: visible;
}

.login-admin textarea {
    width: 100%;
    height: 200px;
    resize: vertical;
    border: none;
    outline: none;
}

@keyframes errorBox {
    from {
        opacity: 0;
        color: #000000;
    }

    to {
        opacity: 1;
        color: #ff0000;
    }
}