#about,
#contact,
#services {
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 80px;
    margin-top: 70px;
}

#privacy-policy {
    margin: 70px 30px 50px 30px;
}

/* for text editor */

body.te {
    margin: 0;
    padding: 0;
    font-size: 16px;
    overflow: hidden;
}

.te-page-header {
    margin: 80px 10px 10px 10px;
}

section.te-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    background: #005050;
    width: 100%;
    overflow: visible;
    height: 70px;
    position: fixed;
    top: 0;
}

section.te-nav * {
    color: white;
}

.online-te-hdr,
.online-te-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.online-te-file-name-display {
    margin-left: 15px;
    font-size: 13px;
    display: block;
    color: #ffff00 !important;
}

.online-te-file-name-display.small-screen {
    display: none;
}


button.te-sfd {
    background-color: rgba(0, 0, 0, 0.3);
    outline: none;
    border: none;
    padding: 4px 8px;
    font-size: 15px;
    cursor: pointer;
    border-radius: 3px;
}

body.te textarea {
    width: calc(100% - 20px);
    height: calc(100vh - 260px);
    max-height: calc(100vh - 260px);
    padding: 10px;
    padding-bottom: 40px;
    outline: none;
    border: none;
    resize: none;
    background: #d3d6d6;
}

.te .settings-trigger {
    position: fixed;
    top: 10px;
    right: 20px;
    color: white;
    cursor: pointer;
}

.te .settings-trigger svg {
    width: 40px;
    height: 40px;
    fill: #fff;
}

.te-settings {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    background: #00000066;
    padding: 20px 0;
}

.te-settings-container {
    background: #ccc;
    width: 90%;
    padding: 5px 10px;
    border-radius: 9px;
    box-shadow: 0 0 2px 3px #005050;
    overflow-y: scroll;
}

.te-settings input {
    text-align: center;
}

.te-settings input[type="number"] {
    width: 50px;
}

.te-note {
    font-style: italic;
    font-size: 0.9rem;
    margin: 5px 10px;
    text-shadow: 0 0 2px #72caef;
}

.te-settings input[type="color"] {
    outline: none;
    border: none;
    width: 80px;
}


/* end Online Text Editor */

/* header menu home */
.header-menu {
    display: none;
}

/* header menu other pages */

.main-nav {
    display: none;
    flex-direction: column;
    justify-content: center;
    background: #008080;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
}

.main-nav-holder {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.main-nav a {
    text-decoration: underline;
    margin-left: 20px;
    color: #fff;
    white-space: nowrap;
    text-decoration-color: #56b2fd;
    text-decoration-thickness: 1px;
    text-decoration-style: dotted;
    text-underline-offset: 5px;
}

.nav a:hover {
    text-decoration: underline;
}

/* side menu */

.menu-icon-home {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 10px;
}

.menu-icon, .menu-icon-lg {
    width: 40px;
    height: 40px;
    margin-left: 10px;
    cursor: pointer;
}

.top-menu-rest {
    background: var(--bc);
    position: fixed;
    top: 5px;
    width: 100%;
    height: 40px;
    margin-bottom: 10px;
}

.menu-icon svg, .menu-icon-lg svg {
    width: inherit;
    height: inherit;
}

.menu-icon svg path, .h svg path {
    fill: var(--bc);
}

.top-menu-rest .menu-icon svg path {
    fill: #fff;
}

.menu-wrapper {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    opacity: 0;
    width: 0%;
    height: 100vh;
    overflow: hidden;
    background: #00505099;
    transition: width 2s, opacity 2s;
    z-index: 10;
}

.menu-wrapper-content {
    width: 50%;
    height: 100vh;
    background: #005050;
    white-space: nowrap;
}

.menu-wrapper.open {
    width: 100%;
    opacity: 1;
}

.menu-wrapper-content-hdr {
    height: 70px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px 15px;
}

.menu-wrapper-content-items {
    margin-left: 30px;
    margin-top: 20px;
    line-height: 32px;
    color: #fff;
}

.side-menu-items {
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    white-space: nowrap;
}

.line-bar {
    margin: 0 5px;
}
.menu-wrapper-content-items a {
    color: #56b2fd;
}

.menu-wrapper-content-hdr img {
    width: 100px;
    height: auto;
}

.side-menu-close-btn {
    padding: 3px 9px;
    background: transparent;
    border-radius: 27px;
    text-shadow: 0 0 3px #fff;
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
    color: #f00;
}

.qrcg input {
    width: 100%;
    padding: 3px;
    border: 2px solid #009090;
    outline: none;
}

.qrcg input[type="color"],
.qrcg input[type="number"] {
    width: 70px;
    text-align: center;
}

.qrcg input[type="date"] {
    width: 130px;
    text-align: center;
}

.qrcg input[type="file"] {
    max-width: 350px;
}

.qrcg input[type="color"] {
    border: none;
    background-color: #fff;
    height: 35px;
}

.qrcg input[type="checkbox"] {
    width: 20px;
    height: 20px;
    padding: 2px;
}

.qrcg textarea {
    width: calc(100% - 20px);
    height: 60px;
    resize: vertical;
    padding: 10px;
    min-height: 40px;
    border: none;
    outline: 1px solid #008080;
}

.qrcg textarea:focus, .qrcg input:focus {
    outline: 2px solid #005050;
}

.about-txt {
    overflow-x: hidden;
}

.moving-txt-parent {
    width: 100%;
    color: #00f0f0;
    text-align: center;
    font-size: 50px;    
}

.moving-txt {
    overflow: hidden;
    text-wrap: nowrap;
    white-space: nowrap;
    animation-name: movingText;
    animation-duration: 11s;
    animation-iteration-count: infinite;
    animation-delay: 1s;
    padding-left: 10px;
    padding-top: 20px;
    padding-bottom: 20px;
    text-shadow: 2px 2px 2px #fff;
}

.about-txt .pic img {
    min-width: 200px;
    border-radius: 9px;
}

.about-txt .pic img:hover {
    outline: 2px solid #008080;
    box-shadow: 0 0 12px 8px #00f0f0;
}

.about-txt .pic img.abt-img {
    border-bottom-right-radius: 30px;
    border-bottom-left-radius: 30px;
}

.about-txt .des, .about-txt .pic {
    width: 45%;
}

 /* .aq-f {
    position: absolute; 
    width: 45%;
} */

.img-txt-over {
    position: relative;
    top: -75%;
    width: 100%;
    height: 75%;
    background: #0008;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
}

.img-txt-over div {
    font-size: 25px;
    padding: 20px;
    text-wrap: nowrap;
    text-align: center;
    line-height: 50px;
    background: blue;
}


@keyframes movingText {
    0% {
        width: 0px;
    }
    
    100% {
        width: 100%;
    }
}

@media only screen and (max-width: 650px) {
    .moving-txt-parent {
        font-size: 40px;
    }
    
    .about-txt .des, .about-txt .pic {
        width: 100%;
    }
    
    .about-txt .rev-sm {
        flex-direction: column-reverse;
    }
}

@media only screen and (max-width: 550px) {
    
    .moving-txt-parent {
        font-size: 25px;
    }
    
    .menu-wrapper-content {
        width: 100%;
    }

    section.te-nav {
        flex-direction: column;
        align-items: flex-start;
        height: 100px;
    }

    .te-page-header {
        margin-top: 112px;

    }

    body.te textarea {
        height: calc(100vh - 330px);
        max-height: calc(100vh - 330px);
    }

    .te-page-header h1 {
        font-size: 18px;
    }

    .te-page-header p {
        font-size: 12px;
    }

    .online-te-file-name-display {
        display: none;
    }

    .online-te-nav {
        padding: 5px;
    }

    .online-te-hdr {
        padding-left: 12px;
        padding-top: 5px;
    }

    .online-te-file-name-display.small-screen {
        display: block;
    }

    .te-title {
        font-size: 16px;
    }
}

/* @media only screen and (max-height: 700px) {
    .menu-wrapper-content {
        overflow-y: scroll;
    }
}

*/
@media screen and (min-width: 550px) {
    body.te textarea {
        font-size: 17px;
    }

    .te-title {
        font-size: 25px;
        margin-left: 20px;
        margin-right: 20px;
    }

    .ms-8.te-title {
        margin-left: 15px;
    }

    .te-nav-h {
        margin-left: 20px;
    }

    .header-menu {
        display: block;
    }

    .menu-icon,
    .menu-icon-home,
    .top-menu-rest {
        display: none;
    }

    .main-nav {
        display: flex;
    }
}

@media only screen and (max-width: 300px) {
    body.te textarea {
        height: calc(100vh - 355px);
        max-height: calc(100vh - 355px);
    }
}

