/* colors */

:root {
    --white: #fff;
    --black: #000;
    --lblack: #222;
    --gray: #aaa;
    --red: #f00;
    --dred: #a00;
    --lred: #f20;
    --green: #0f0;
    --dgreen: #0a0;
    --lgreen: #3f3;
    --blue: #00f;
    --dblue: #00a;
    --lblue: #04f;
    --yellow: #ff0;
    --dyellow: #aa0;
    --lyellow: #ff5;
    --purple: #f0f;
    --dpurple: #a0a;
    --lpurple: #f5f;
    --turquoise: #0ff;
    --bc: #008080;
    --bc-d: #093f3f;
    --footer-bc: #003333;
    --header-bc: #003333;
    --border-c: #000033;
    --fc: #ffffff;
    --fc-d: #022;
}

/* tools */

.closer {
    border-radius: 50%;
    padding: 5px;
    background: red;
    height: 15px;
    width: 15px;
    text-align: center;
}

#closer-unhider {
    position: fixed;
    top: 0;
    width: 100%;
    overflow: hidden;
    height: 10px;
    z-index: 100;
}

/* for custom alert */
.custom-alert {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: transparent;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 10;
}

.ca-container {
    width: 80vw;
    max-width: 600px;
    height: auto;
    padding: 5px 5px 15px 5px;
    border-radius: 5px;
    box-shadow: 0px 0px 6px 3px teal;
    background: #fff;
    color: #022424;
}

.ca-title {
    font-weight: bold;
}

.ca-description {
    font-size: 15px;
}

.ca-ok {
    float: right;
    padding: 5px 15px;
    background: rgb(1, 125, 249);
    border-radius: 5px;
    box-shadow: 0px 0px 6px 3px skyblue;
    color: #fff;
    cursor: pointer;
}

/* end custom alert */

.back-btn {
    margin: 20px;
}

.blue {
    color: #00f;
}

.gold {
    color: gold;
}

.dark-gold {
    color: rgb(249, 142, 21);
}

.link {
    color: var(--content-link-c);
}

.highlight-gold {
    background: #fbd603;
    background-color: #fbd603;
    color: #000;
}

.code-holder {
    height: 190px;
    border: 1px solid gainsboro;
    outline: 1px solid gainsboro;
    resize: none;
}

.copy-btn {
    padding: 5px 10px;
    background: #017df9;
    border-radius: 5px;
    box-shadow: 0px 0px 6px 3px skyblue;
    color: #fff;
    cursor: pointer;
    border: none;
    text-align: center;
}

.btn {
    padding: 5px 10px;
    border-radius: 5px;
    box-shadow: 0px 0px 6px 3px skyblue;
    cursor: pointer;
    border: none;
    text-align: center;
    user-select: none;
}

.btn-cancel {
    background: #fa7f03;
    color: #fff;
}

.btn-reset,
.btn-delete {
    background: #fa2803;
    color: #fff;
}

.btn-ok {
    background: #017df9;
    color: #fff;
}

.btn-green {
    background: #078507;
    color: #fff;
}

.btn:hover {
    outline: 1px solid rgba(0, 0, 0, 0.409);
}

