.big-pic {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: #008080;
}

article.home .first-CE {
    margin: 10px 20px;
}

.online-tools {
    margin: 10px 20px 20px 20px;
}

.online-tools div div {
    margin-bottom: 10px;
    border-bottom: 1px solid #0005;
    padding-bottom: 7px;
}

.big-pic img {
    border-radius: 50%;
}

.hdr {
    text-align: center;
}

.short-intro,
.nav {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: center;
    margin: 15px 0 30px 0;
    line-height: 30px;
}

.nav nav a {
    text-decoration: none;
    margin-left: 20px;
}

.home-second-nav {
    margin-left: 9px;
    margin-right: 9px;
    text-align: center;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-around;
    flex-wrap: wrap;
    white-space: nowrap;
}

.home-second-nav span {
    padding: 5px;
}

.home-second-nav a {
    margin-left: 0 !important;
}

.showcase {
    background: linear-gradient(157deg, #099, #055 50%, #099 100%);
    /* height: 350px; */
    padding: 10px 0;
}

#logo_txt {

    width: 300px;
    /* border-radius: 15px; */
    /* font-size: 65px; */
    /* padding: 9px 15px; */
    white-space: nowrap;
    margin-left: 18px;
    /* letter-spacing: 9px; */
    /* background: #fff; */
    /* font-family: serif; */
}

#logo_txt img {
    width: 100%;
    margin: 0;
    padding: 0;
}

#logo_txt div,
#logo_txt span {
    text-shadow: 2px -9px 3px rgb(45, 150, 242);
}

.logo-2de {
    letter-spacing: 9px;
    margin-left: 11px;
}

.lspc-4 {
    letter-spacing: 4px;
}

.home-text {
    padding-left: 20px;
    padding-right: 20px;
    line-height: 25px;
    word-spacing: 3px;
}

.show-case-txt {
    display: flex;
    align-items: flex-start;
    line-height: normal;
}

/* .show-case-txt div:first-child {
    margin-top: -23px;
} */

a svg {
    /* fill: #444746; */
    fill: #42a5fc;
    height: 1.5rem;
    margin-bottom: 0.125rem;
    margin-left: 0.5938rem;
    vertical-align: middle;
    width: 1.5rem;
}

.laptops {
    padding: 10px 20px;
    line-height: 40px;
    text-align: center;
}

.content-section {
    padding: 10px 20px;
}

.content-section h1.title {
    text-align: center;
    font-size: 1.6em;
    line-height: 35px;
}

.affiliate-links-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}

.affiliate-links-container .right,
.affiliate-links-container .left {
    width: 50%;
    /* min-width: 300px; */
}

.affiliate-links-container img {
    width: 100%;
}

.affiliate-links-wrapper div {
    margin-top: 11px;
}

.group-projects,
.group-services,
.group-courses,
.group-online-tools {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* display: grid;
	grid-template-columns: auto auto auto; */
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-bottom: 30px;
}

.android-app,
.pro-service,
.pro-course,
.pro-tools {
    border-radius: 2vw;
    margin: 20px 10px;
    width: 300px;
    height: 300px;
    box-shadow: 0px 0px 5px 2px #11d7d1;
}

.pro-service {
    box-shadow: 0px 0px 5px 2px #0147f8;
}

.pro-course {
    box-shadow: 0px 0px 5px 2px #018e0f;
}

.android-app {
    box-shadow: 0px 0px 5px 2px #8e0187;
}

.pro-service img,
.pro-course img,
.pro-tools img {
    width: 95%;
    height: auto;
}

.pro-tools img {
    border-radius: 13px;
}

.android-app-logo {
    width: 120px;
    height: 120px;
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 9px;
}

.android-app:hover,
.pro-service:hover,
.pro-course:hover {
    box-shadow: 0 0 10px 6px #088;
    background: #055;
    color: #fff;
}

.pro-tools:hover {
    box-shadow: 0 0 10px 6px #017382;
    background: #00b8d1;
    color: #fff;
}

.pro-tools:hover a {
    color: #fff;
}



@media only screen and (max-width:1000px) {

    .affiliate-links-container .right,
    .affiliate-links-container .left {
        width: 100%;
    }

    .affiliate-links-container img {
        margin-top: 15px;
        margin-bottom: 15px;
    }
}

@media only screen and (max-width: 550px) {
    .showcase .first {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    #logo_txt {
        margin-left: 0;
        width: 200px;
    }

    .fs-2 {
        font-size: 1.3rem;
    }

    .laptops {
        padding: 10px 20px;
    }


}